import commentModule from '@/components/commentModule/commentModule.vue';
import shoppublic from '@/common/shoppublic.js';
export default{
	components:{
		commentModule:commentModule
	},
	data(){
		return{
			imageUrl:shoppublic.getImageUrl(),
			partner: [ // 寻合伙人列表
				{
					imgUrl: '/images/jsr_hei.png',
					partnerType: '技术人',
					value: '',
					status: 2
				},
				{
					imgUrl: '/images/chr_hei.png',
					partnerType: '策划人',
					value: '',
					status: 2
				},
				{
					imgUrl: '/images/glr_hei.png',
					partnerType: '管理人',
					value: '',
					status: 2
				},
				{
					imgUrl: '/images/yyr_hei.png',
					partnerType: '运营人',
					value: '',
					status: 2
				},
				{
					imgUrl: '/images/yxr_hei.png',
					partnerType: '营销人',
					value: '',
					status: 2
				},
				{
					imgUrl: '/images/tzr_hei.png',
					partnerType: '投资人',
					value: '',
					status: 2
				}
			],
			pagesInfo:[], //当前页面的信息
		}
	},
	onLoad(option) {
		console.log(option);
		this.getPageInfo(option.key);
	},
	methods:{
		// 获取页面信息
		getPageInfo(id){
			var _this = this;
			uni.request({
				url: shoppublic.getUrl() + '/partnershipShop/findPartnershipShopByid',
				data: {
					token:6239,//登陆用户信息
					id:id //当前信息id
				},
				success: res => {
					console.log(res.data.responseBody);
					_this.pagesInfo.push(res.data.responseBody);
					_this.partnerList(res.data.responseBody.value);
				},
			});
		},
		// 替换partner的url
		// value:从后台拿到的寻合伙人列表
		partnerList(value) {
			let _thisData = this;
			for (let i = 0; i < _thisData.partner.length; i++) {
				for (let j = 0; j < value.length; j++) {
					if (_thisData.partner[i].partnerType == value[j].name) {
						_thisData.partner[i].imgUrl = value[j].url1;
						_thisData.partner[i].value = value[j];
						_thisData.partner[i].status = 1;
					}
				}
			}
			// this.sortPartner(_thisData.partner);
			// 替换data.partner
			this.partner = this.sortPartner(_thisData.partner);
			console.log(this.partner);
		},
		// 根据partner的status状态进行升序排序(注:排序是为了将用户选中的合伙人类型放在数组最前头)
		sortPartner(arr) {
			for (let i = 0; i < arr.length - 1; i++) {
				for (let j = 0; j < arr.length - 1 - i; j++) {
					if (arr[j].status > arr[j + 1].status) {
						var temp = arr[j];
						arr[j] = arr[j + 1];
						arr[j + 1] = temp;
					}
				}
			}
			return arr;
		},
		switchStyle(index,bool){
			this.partnersList[index].bool = !this.partnersList[index].bool;
		}
	}
}
